<template>
  <div :class="$root.themeHomeChange === '1' ? 'defect-form_light' : 'defect-form'">
    <el-form
      :inline="true"
      class="formList"
      :model="formData"
      label-position="right"
      label-width="120px"
      :rules="rules[stage]"
      :key="stage"
      ref="ruleForm"
      v-loading="loading"
      element-loading-text="拼命加载中"
      element-loading-spinner="el-icon-loading"
      element-loading-background="rgba(0, 0, 0, 0.8)">
      <!-- <el-button type="primary" style="float: left;">生成报告</el-button> -->
      
      <!-- 确认整改 -->
      <div v-if="isRectification === 'isTrue'">
        <!-- 取证阶段 -->
        <div v-if="stage == '全部' ? true : stage === '取证阶段'">
          <div class="title">取证阶段</div>
          <el-form-item label="责任部门: " class="formItem">
            <span class="formText">{{formData.qzZrbm}}</span>
          </el-form-item>
          <el-form-item
            label="是否问责: "
            class="formItem-right"
            label-width="200px"
            prop="ifAccountability">
            <el-radio-group v-model="formData.ifAccountability">
              <el-radio :label="1">是</el-radio>
              <el-radio :label="0">否</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="取证人: " class="formItem">
            <span class="formText">{{formData.qzPerson}}</span>
          </el-form-item>
          <el-form-item
            label="整改指引建议: "
            class="formItem-right"
            label-width="200px"
            prop="modifyOpinion">
            <el-input
              type="textarea"
              :autosize="{ minRows: 2, maxRows: 4}"
              placeholder="请输入"
              clearable
              v-model="formData.modifyOpinion">
            </el-input>
          </el-form-item>
          <el-form-item
            label="取证材料说明: "
            class="formItem"
            prop="qzMaterialDesc">
            <el-input
              type="textarea"
              :autosize="{ minRows: 2, maxRows: 4}"
              placeholder="请输入"
              clearable
              v-model="formData.qzMaterialDesc">
            </el-input>
          </el-form-item>
          <el-form-item
            label="需修订法律法规/制度规范等: "
            class="formItem-right"
            label-width="200px"
            prop="ifXdflfgZdgf">
            <el-radio-group v-model="formData.ifXdflfgZdgf">
              <el-radio :label="1">是</el-radio>
              <el-radio :label="0">否</el-radio>
            </el-radio-group>
          </el-form-item>
          <!-- 上传控件并列 -->
          <el-row>
            <!-- qzProcessFileList -->
            <!-- :action="fileUrl + defectIds.join(',')" -->
            <el-col :span="12">
              <el-form-item
                label="上传附件: "
                class="formItem"
                style="width: calc(100% - 10px);"
                :prop="this.fileOption[this.stage]"
                :key="Array.isArray(formData[this.fileOption[this.stage]]) && formData[this.fileOption[this.stage]].lenght">
                <el-upload
                  ref="uploadObj"
                  :action="fileUrl + defectIds.join(',')"
                  list-type="picture-card"
                  :on-success="handleSuccess"
                  :on-error="handleError"
                  :file-list="formData.fileList"
                  :auto-upload="true"
                  accept=".xls, .xlsx, .docx, .pdf"
                  :before-upload="beforeUpload"
                  style="display: flex; flex-wrap: wrap;">
                  <i slot="default" class="el-icon-plus">
                    <p>上传附件</p>
                  </i>
                  <div slot="file" slot-scope="{file}">
                    <div class="fileClass">
                      <i class="el-icon-document">
                        <el-tooltip class="btn-trip" effect="dark" :content="file.name" placement="bottom">
                          <span >
                            <p>{{file.name}}</p>
                          </span>
                        </el-tooltip>
                      </i>
                      <i class="el-icon-download" @click="projectDownload(file)">
                        <el-tooltip class="btn-trip" effect="dark" :content="file.name" placement="bottom">
                          <span>
                            <p>下载文件</p>
                          </span>
                        </el-tooltip>
                      </i>
                      <el-popconfirm
                        :popper-class="$root.themeHomeChange === '1' ? 'popconfirmClass_light' : 'popconfirmClass'"
                        confirm-button-text='确定'
                        cancel-button-text='取消'
                        icon="el-icon-info"
                        icon-color="red"
                        :title="`是否删除 ${file.name} ？`"
                        @confirm="deleteUploadItem(file)"
                      >
                        <i slot="reference" class="deleteIcon el-icon-circle-close"></i>
                      </el-popconfirm>
                    </div>
                  </div>
                </el-upload>
                <p style="color: #bbb; font-size: 10px;">文件后缀名可为xls 或xlsx （即Excel格式）、word、pdf等，文件大小不得大于30M。</p>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-row>
                <el-col :span="24">
                  <el-form-item
                    label="归口部门: "
                    class="formItem-right"
                    style="width: calc(100% - 10px);"
                    label-width="200px"
                    prop="accountabilityDepartment">
                    <el-select
                      size="mini"
                      v-model="formData.accountabilityDepartment"
                      placeholder="请选择"
                      style="width: 180px;"
                      clearable
                    >
                      <el-option label="办公室" value="办公室"></el-option>
                      <el-option label="合规与监审部" value="合规与监审部"></el-option>
                      <el-option label="系统规划部" value="系统规划部"></el-option>
                      <el-option label="资产管理部" value="资产管理部"></el-option>
                      <el-option label="市场营销部" value="市场营销部"></el-option>
                      <el-option label="安全监管部" value="安全监管部"></el-option>
                      <el-option label="人力资源部" value="人力资源部"></el-option>
                      <el-option label="党建工作部" value="党建工作部"></el-option>
                      <el-option label="创新与数字化部" value="创新与数字化部"></el-option>
                      <el-option label="财务部" value="财务部"></el-option>
                    </el-select>
                  </el-form-item>
                </el-col>
                <el-col :span="24">
                  <el-form-item
                    label="需修订建议说明: "
                    class="formItem-right"
                    style="width: calc(100% - 10px);"
                    label-width="200px"
                    prop="revisedOpinion">
                    <el-input
                      type="textarea"
                      :autosize="{ minRows: 2, maxRows: 4}"
                      placeholder="请输入"
                      clearable
                      v-model="formData.revisedOpinion">
                    </el-input>
                  </el-form-item>
                </el-col>
              </el-row>
            </el-col>
          </el-row>
        </div>
        
        <el-divider v-if="stage == '全部'"></el-divider>
        
        <!-- 确认阶段 -->
        <div v-if="stage == '全部' ? true : stage === '确认阶段'">
          <div class="title">确认阶段</div>
          <el-row>
            <el-col :span="5">
              <el-form-item
                label="责任部门: "
                class="formItem"
                style="width: calc(100% - 10px);">
                <span class="formText">{{formData.confirmZrbm}}</span>
              </el-form-item>
            </el-col>
            <el-col :span="5">
              <el-form-item
                label="确认人: "
                class="formItem"
                style="width: calc(100% - 10px);">
                <span class="formText">{{formData.confirmPerson}}</span>
              </el-form-item>
            </el-col>
          </el-row>
          <el-form-item
            label="审批意见: "
            class="formItem"
            prop="spOpinion">
            <el-input
              type="textarea"
              :autosize="{ minRows: 2, maxRows: 4}"
              placeholder="请输入"
              clearable
              v-model="formData.spOpinion">
            </el-input>
          </el-form-item>
        </div>
        
        <el-divider v-if="stage == '全部'"></el-divider>
        
        <!-- 整改阶段 -->
        <div v-if="stage == '全部' ? true : stage === '整改阶段'">
          <div class="title">整改阶段</div>
          <el-row>
            <el-col :span="5">
              <el-form-item
                label="问责部门: "
                class="formItem"
                style="width: calc(100% - 10px);">
                <span class="formText">{{formData.zgZrbm}}</span>
              </el-form-item>
            </el-col>
            <el-col :span="5">
              <el-form-item
                label="整改人: "
                class="formItem"
                style="width: calc(100% - 10px);">
                <span class="formText">{{formData.zgPerson}}</span>
              </el-form-item>
            </el-col>
            <el-col :span="5">
              <el-form-item
                label="是否问责: "
                class="formItem"
                style="width: calc(100% - 10px);">
                <span class="formText">{{formData.ifAccountability ? '是' : '否'}}</span>
              </el-form-item>
            </el-col>
          </el-row>
          <el-form-item
            label="整改意见: "
            class="formItem"
            prop="zgOpinion">
            <el-input
              type="textarea"
              :autosize="{ minRows: 2, maxRows: 4}"
              placeholder="请输入"
              clearable
              v-model="formData.zgOpinion">
            </el-input>
          </el-form-item>
          <el-form-item
            label="上传附件: "
            class="formItem-right"
            label-width="200px"
            :prop="this.fileOption[this.stage]"
            :key="Array.isArray(formData[this.fileOption[this.stage]]) && formData[this.fileOption[this.stage]].lenght">
            <el-upload
              ref="uploadObj"
              :action="fileUrl + defectIds.join(',')"
              list-type="picture-card"
              :on-success="handleSuccess"
              :on-error="handleError"
              :file-list="formData.fileList"
              :auto-upload="true"
              accept=".xls, .xlsx, .docx, .pdf"
              :before-upload="beforeUpload"
              style="display: flex; flex-wrap: wrap;">
              <i slot="default" class="el-icon-plus">
                <p>上传附件</p>
              </i>
              <div slot="file" slot-scope="{file}">
                <div class="fileClass">
                  <i class="el-icon-document">
                    <el-tooltip class="btn-trip" effect="dark" :content="file.name" placement="bottom">
                      <span >
                        <p>{{file.name}}</p>
                      </span>
                    </el-tooltip>
                  </i>
                  <i class="el-icon-download" @click="projectDownload(file)">
                    <el-tooltip class="btn-trip" effect="dark" :content="file.name" placement="bottom">
                      <span>
                        <p>下载文件</p>
                      </span>
                    </el-tooltip>
                  </i>
                  <el-popconfirm
                    :popper-class="$root.themeHomeChange === '1' ? 'popconfirmClass_light' : 'popconfirmClass'"
                    confirm-button-text='确定'
                    cancel-button-text='取消'
                    icon="el-icon-info"
                    icon-color="red"
                    :title="`是否删除 ${file.name} ？`"
                    @confirm="deleteUploadItem(file)"
                  >
                    <i slot="reference" class="deleteIcon el-icon-circle-close"></i>
                  </el-popconfirm>
                </div>
              </div>
            </el-upload>
            <p style="color: #bbb; font-size: 10px;">文件后缀名可为xls 或xlsx （即Excel格式）、word、pdf等，文件大小不得大于30M。</p>
          </el-form-item>
        </div>
        
        <el-divider v-if="stage == '全部'"></el-divider>
        
        <!-- 修订与反馈 -->
        <div v-if="stage == '全部' ? true : stage === '修订与反馈'">
          <div class="title">修订与反馈</div>
          <el-row>
            <el-col :span="5">
              <el-form-item
                label="归口部门: "
                class="formItem"
                style="width: calc(100% - 10px);">
                <span class="formText">{{formData.revisedFeedbackDepartment}}</span>
              </el-form-item>
            </el-col>
            <el-col :span="5">
              <el-form-item
                label="责任人: "
                class="formItem"
                style="width: calc(100% - 10px);">
                <span class="formText">{{formData.jzZrr}}</span>
              </el-form-item>
            </el-col>
            <el-col :span="10">
              <el-form-item
                label="法律法规/制度规范等修订反馈: "
                class="formItem-right"
                style="width: calc(100% - 10px);"
                label-width="210px"
                prop="flfgZdgfRevisedFeedback">
                <el-radio-group v-model="formData.flfgZdgfRevisedFeedback">
                  <el-radio :label="1">修订</el-radio>
                  <el-radio :label="0">不予修订</el-radio>
                </el-radio-group>
              </el-form-item>
            </el-col>
          </el-row>
          
          <el-row>
            <el-col :span="10">
              <el-form-item
                label="修订与反馈说明: "
                class="formItem"
                prop="revisedFeedback"
                style="width: calc(100% - 10px);">
                <el-input
                  type="textarea"
                  :autosize="{ minRows: 2, maxRows: 4}"
                  placeholder="请输入"
                  clearable
                  v-model="formData.revisedFeedback">
                </el-input>
              </el-form-item>
            </el-col>
            <el-col :span="14">
              <el-form-item
                label="关联需修订的法律法规/制度规范等: "
                class="formItem-right"
                prop="knowledgeIds"
                style="width: calc(100% - 10px);"
                label-width="210px">
                <span class="">已选择 {{ ( formData.knowledgeIds && formData.knowledgeIds.length ) || 0 }} 条</span>
                <span style="margin-left: 72px;">
                  <el-button
                      @click="relationData"
                      icon="el-icon-paperclip"
                  >关联</el-button>
                  <el-button
                    :disabled="
                      (
                        !formData.knowledgeIds
                        ||
                        ( Array.isArray(formData.knowledgeIds) && formData.knowledgeIds.length === 0)
                      )"
                    icon="el-icon-search"
                    @click="showKnowTable"
                  >查看详情</el-button>
                </span>
              </el-form-item>
            </el-col>
          </el-row>
        </div>
      
      </div>
      
      <!-- 不整改流程 -->
      <div v-else>
        <div v-if="stage == '全部' ? true : stage === '不整改举证'">
          <div class="title">不整改举证</div>
          <el-form-item
            label="不进行整改的举证说明: "
            class="formItem-inline"
            label-width="120px"
            prop="notChangeReasonDesc">
            <el-input
              type="textarea"
              :autosize="{ minRows: 3, maxRows: 5}"
              placeholder="请输入"
              clearable
              v-model="formData.notChangeReasonDesc">
            </el-input>
          </el-form-item>
          <el-form-item
            label="上传附件: "
            class="formItem-inline"
            label-width="120px"
            :prop="this.fileOption[this.stage]"
            :key="Array.isArray(formData[this.fileOption[this.stage]]) && formData[this.fileOption[this.stage]].lenght">
            <el-upload
              ref="uploadObj"
              :action="fileUrl + defectIds.join(',')"
              list-type="picture-card"
              :on-success="handleSuccess"
              :on-error="handleError"
              :file-list="formData.fileList"
              :auto-upload="true"
              accept=".xls, .xlsx, .docx, .pdf"
              :before-upload="beforeUpload"
              style="display: flex; flex-wrap: wrap;">
              <i slot="default" class="el-icon-plus">
                <p>上传附件</p>
              </i>
              <div slot="file" slot-scope="{file}">
                <div class="fileClass">
                  <i class="el-icon-document">
                    <el-tooltip class="btn-trip" effect="dark" :content="file.name" placement="bottom">
                      <span >
                        <p>{{file.name}}</p>
                      </span>
                    </el-tooltip>
                  </i>
                  <i class="el-icon-download" @click="projectDownload(file)">
                    <el-tooltip class="btn-trip" effect="dark" :content="file.name" placement="bottom">
                      <span>
                        <p>下载文件</p>
                      </span>
                    </el-tooltip>
                  </i>
                  <el-popconfirm
                    :popper-class="$root.themeHomeChange === '1' ? 'popconfirmClass_light' : 'popconfirmClass'"
                    confirm-button-text='确定'
                    cancel-button-text='取消'
                    icon="el-icon-info"
                    icon-color="red"
                    :title="`是否删除 ${file.name} ？`"
                    @confirm="deleteUploadItem(file)"
                  >
                    <i slot="reference" class="deleteIcon el-icon-circle-close"></i>
                  </el-popconfirm>
                </div>
              </div>
            </el-upload>
            <p style="color: #bbb; font-size: 10px;">文件后缀名可为xls 或xlsx （即Excel格式）、word、pdf等，文件大小不得大于30M。</p>
          </el-form-item>
        </div>
        
        <el-divider v-if="stage == '全部'"></el-divider>
        
        <div v-if="stage == '全部' ? true : stage === '举证确认'">
          <div class="title">举证确认</div>
          <el-form-item
            label="不进行整改的举证说明: "
            class="formItem-inline"
            label-width="120px"
            prop="notChangeReasonDesc">
            <p class="formText" style="font-size: 12px; font-weight: bold;">
              {{ formData.notChangeReasonDesc || '暂无' }}
            </p>
            <!-- <el-input
              disabled
              type="textarea"
              :autosize="{ minRows: 3, maxRows: 5}"
              placeholder="请输入"
              clearable
              v-model="formData.notChangeReasonDesc">
            </el-input> -->
          </el-form-item>
          <el-form-item
            label="举证附件: "
            class="formItem-inline"
            label-width="120px">
            <div class="jzFileList" v-if="Array.isArray(formData.jzFileList) && formData.jzFileList.lenght !== 0">
              <div
                class="fileClass"
                v-for="(item, index) in formData.jzFileList"
                :key="index">
                <i class="el-icon-document">
                  <el-tooltip class="btn-trip" effect="dark" :content="getFileName(item)" placement="bottom">
                    <span>
                      <p>{{ getFileName(item) }}</p>
                    </span>
                  </el-tooltip>
                </i>
                <i
                  class="el-icon-download"
                  @click="projectDownload({
                    name: getFileName(item),
                    url: item
                  })">
                  <el-tooltip class="btn-trip" effect="dark" :content="getFileName(item)" placement="bottom">
                    <span>
                      <p>下载文件</p>
                    </span>
                  </el-tooltip>
                </i>
              </div>
            </div>
            <p style="color: #fff; font-size: 12px; font-weight: bold;" v-else>暂无附件</p>
          </el-form-item>
          <el-row>
            <el-col :span="5">
              <el-form-item
                label="责任部门: "
                class="formItem"
                style="width: calc(100% - 10px);">
                <span class="formText">{{formData.zrbm}}</span>
              </el-form-item>
            </el-col>
            <el-col :span="5">
              <el-form-item
                label="确认人: "
                class="formItem"
                style="width: calc(100% - 10px);">
                <span class="formText">{{formData.stageLeader}}</span>
              </el-form-item>
            </el-col>
          </el-row>
          <el-form-item
            label="举证是否有效: "
            class="formItem-right"
            label-width="120px"
            prop="jzIfEffective">
            <el-radio-group v-model="formData.jzIfEffective">
              <el-radio :label="1">是</el-radio>
              <el-radio :label="0">否</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item
            label="确认意见: "
            class="formItem-inline"
            prop="jzConfirmOpinion">
            <el-input
              type="textarea"
              :autosize="{ minRows: 3, maxRows: 5}"
              placeholder="请输入"
              clearable
              v-model="formData.jzConfirmOpinion">
            </el-input>
          </el-form-item>
        </div>
      </div>
      
    </el-form>
    
    <!-- 按钮集合 -->
    <div style="text-align: center; margin: 20px 0;">
      <el-button
        type="primary"
        size="mini"
        @click="submitForm(0)"
      >保存</el-button>
      <el-button
        type="primary"
        size="mini"
        @click="submitForm(1)"
      >提交</el-button>
      <el-button
        :disabled="stage === '取证阶段' || stage === '不整改取证'"
        size="mini"
        @click="submitForm(-1)"
      >退回</el-button>
      <el-button
        size="mini"
        @click="resetForm()"
      >取消</el-button>
    </div>
    
    <!-- 查看详细表格 -->
    <el-dialog
      title="关联修订已选列表"
      :visible.sync="dialogTableVisible"
      :modal="false"
      v-loading="tableLoading"
      element-loading-text="拼命加载中"
      element-loading-spinner="el-icon-loading"
      element-loading-background="rgba(0, 0, 0, 0.8)">
      <el-table
        :data="knowTabel"
        style="width: 100%;"
        tooltip-effect="light"
        :height="tableHeight">
        <el-table-column
          prop="knowledgeName"
          label="知识库名称"
          min-width="300"
          show-overflow-tooltip
          align="center"
        ></el-table-column>
        <el-table-column
          prop="version"
          label="最新版本号"
          min-width="100"
          show-overflow-tooltip
          align="center"
        ></el-table-column>
        <el-table-column
          prop="knowledgeType"
          label="知识库类别"
          min-width="120"
          show-overflow-tooltip
          align="center"
        ></el-table-column>
        <el-table-column
          prop="systemBusinessType"
          label="制度业务类别"
          min-width="200"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="systemFunctionOrientation"
          label="制度功能定位"
          min-width="200"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="bbjg"
          label="颁布机构"
          min-width="200"
          align="center"
        ></el-table-column>
        <el-table-column
          prop="bbsj"
          label="颁布时间"
          min-width="100"
          show-overflow-tooltip
          align="center"
        ></el-table-column>
        <el-table-column
          prop="updateStatus"
          label="修订状态"
          min-width="80"
          show-overflow-tooltip
          align="center"
        >
          <template slot-scope="scope">{{scope.row.updateStatus===0?'待修订':'已修订'}}</template>
        </el-table-column>
        <el-table-column
          prop="department"
          label="所属部门"
          min-width="120"
          show-overflow-tooltip
          align="center"
        ></el-table-column>
        <el-table-column
          prop="zrgw"
