<script>
  import Interface from "@/base/mixin/disposal-center.js";
  import { API_PATH } from '@/config';
  import proxyJSON from '../../../../config/vue/proxy.js'
  export default {
    name: "formList",
    components: {},
    mixins: [Interface],
    props: {
      data:{
        type:Object,
        default:()=>{
          return {}
        }
      },
      stage: {
        type:String,
        default:()=>{
          return ''
        }
      },
      defectIds: {
        type:Array,
        default:()=>{
          return [];
        }
      },
      isRectification: {
        type:String,
        default:()=>{
          return 'isTrue'
        }
      },
      loading: {
        type:Boolean,
        default:()=>{
          return false;
        }
      },
      orderId: {
        type: String,
        default: () => {
          return ''
        }
      }
    },
    data() {
      return {
        tableLoading: false,
        knowTabel: [],
        fileUrl: API_PATH + '/disposal/file/upload?defectStr=',
        formLoading: false,
        tableHeight: (window.innerHeight) * 0.6 - 70,
        dialogTableVisible: false,
        formData: {},
        fileOption: {
          '取证阶段': 'qzProcessFileList',
          '整改阶段': 'zgFileList',
          '不整改举证': 'jzFileList',
        },
        processCodeOption: {
          '全部': 0,
          '取证阶段': 1,
          '确认阶段': 2,
          '整改阶段': 3,
          '修订与反馈': 4,
          '不整改举证': 5,
          '举证确认': 6
        },
        rules: {
          '取证阶段': {
            ifAccountability: [
              { required: true, message: '请选择', trigger: 'change' }
            ],
            modifyOpinion: [
              { required: true, message: '请输入', trigger: 'blur' }
            ],
            qzMaterialDesc: [
              { required: true, message: '请输入', trigger: 'blur' }
            ],
            ifXdflfgZdgf: [
              { required: true, message: '请选择', trigger: 'change' }
            ],
            qzProcessFileList: [
              { required: true, message: '请上传', trigger: 'change' }
            ],
            accountabilityDepartment: [
              { required: true, message: '请选择', trigger: 'change' }
            ],
            revisedOpinion: [
              { required: true, message: '请输入', trigger: 'blur' }
            ],
          },
          
          '确认阶段': {
            spOpinion: [
              { required: true, message: '请输入', trigger: 'blur' }
            ]
          },
          
          '整改阶段': {
            zgOpinion: [
              { required: true, message: '请输入', trigger: 'blur' }
            ],
            zgFileList: [
              { required: true, message: '请上传', trigger: 'change' }
            ],
          },
          
          '修订与反馈': {
            flfgZdgfRevisedFeedback: [
              { required: true, message: '请选择', trigger: 'change' }
            ],
            revisedFeedback: [
              { required: true, message: '请输入', trigger: 'blur' }
            ],
            knowledgeIds: [
              { required: true, message: '请关联所要修订的内容', trigger: 'change' }
            ]
          },
          
          '不整改举证': {
            notChangeReasonDesc: [
              { required: true, message: '请输入不进行整改的举证说明', trigger: 'blur' }
            ],
            jzFileList: [
              { required: true, message: '请上传', trigger: 'change' }
            ],
          },
          
          '举证确认': {
            jzIfEffective: [
              { required: true, message: '请选择', trigger: 'change' }
            ],
            jzConfirmOpinion: [
              { required: true, message: '请输入确认意见', trigger: 'blur' }
            ],
          },
        }
      }
    },
    watch: {
      loading(v) {
        this.formLoading = v;
      },
      stage(v) {
        let fileList = (Array.isArray(this.formData[this.fileOption[v]]) && this.formData[this.fileOption[v]].map(item => {
          return {
            name: this.getFileName(item),
            url: item
          }
        })) || [];
        this.formData.fileList = fileList;
        // window.console.log(v, fileList, this.formData);
        this.$forceUpdate();
      },
      'data': {
        handler(v) {
          // window.console.log(v, ov);
          // if(this.stage == '取证阶段') {
          // } else if(this.stage == '确认阶段') {
          // } else if(this.stage == '整改阶段') {
          // } else if(this.stage == '修订与反馈') {
          // }
          let fileList = (Array.isArray(v[this.fileOption[this.stage]]) && v[this.fileOption[this.stage]].map(item => {
            return {
              name: this.getFileName(item),
              url: item
            }
          })) || [];
          this.formData = JSON.parse(JSON.stringify({...v, fileList, [this.fileOption[this.stage]]: []}));
          
        },
        deep: true,
        immediate: false
      },
      'isRectification': {
        handler(v) {
          this.formData.ifDisposal = v == 'isTrue' ? 1 : 0;
        },
        deep: true,
        immediate: true
      },
      'formData.fileList': {
        handler(v) {
          // console.log('深度监听', v);
          this.formData[this.fileOption[this.stage]] = v.map(item => item.url);
        },
        deep: true
      }
    },
    mounted() {},
    created() {},
    methods: {
      getFileName(path) { 
        let pos1 = path.lastIndexOf('/'); 
        let pos2 = path.lastIndexOf('\\'); 
        let pos = Math.max(pos1, pos2); 
        if (pos < 0) { 
          return path; 
        } 
        else { 
          return path.substring(pos + 1); 
        } 
      },
      // 下载已上传附件
      projectDownload(file) {
        window.console.log(file, API_PATH);
        
        if(!file.url) {
          return;
        }
        
        window.open(`${ proxyJSON[API_PATH].target }/disposal/file/download?filePath=${file.url}&openStyle=inline`);
        
      },
      download (data, file) {
        if (!data) {
          return;
        }
        let url = window.URL.createObjectURL(new Blob([data]))
        let link = document.createElement('a')
        link.style.display = 'none'
        link.href = url
        link.id='Adownload'
        link.setAttribute('download', file.name) //命名可能会出现问题，格式一定和后端下载的格式一样
        
        document.body.appendChild(link)
        link.click()
        document.getElementById('Adownload').remove();
      },
      // 删除上传列表
      deleteUploadItem(file) {
        window.console.log(file, this.formData.fileList);
        let uploadFiles = this.$refs['uploadObj'].uploadFiles;
        
        // 清空组件中的数据
        uploadFiles.splice(uploadFiles.indexOf(file), 1);
        // 清空列表中的数据
        window.console.log(this.formData.fileList);
        this.formData.fileList.splice(this.formData.fileList.indexOf(file), 1);
        // let index = this.formData.fileList.some((item, index) => {
        //   if(item.name === file.name){
        //     return index + 1;
        //   }
        // })
        // this.formData.fileList.splice(index, 1)
      },
      // 表单按钮
      submitForm(type) {
        let params = JSON.parse(JSON.stringify({
          ...this.formData,
          defectIds: this.defectIds,
          buttonCode: type,
          disposalProcessName: this.stage,
          processCode: this.processCodeOption[this.stage],
          orderId: this.orderId
        }));
        
        
        if(type == 'cancel') {
          this.$emit('cancel');
          return false;
        } else if(type === -1) {
          this.setRectifyProcess(params, type);
          return;
        }
        
        this.$refs['ruleForm'].validate((valid) => {
          if (valid) {
            this.formLoading = true;
            this.setRectifyProcess(params, type);
            // this.$message(`所处阶段：${this.stage} ，点击事件类型 -- ${type}`);
          } else {
            window.console.log('error submit!!', this.formData);
            return false;
          }
        });
      },
      // 发起表单流程请求
      setRectifyProcess(params, status) {
        let type = {
          0: '保存',
          1: '提交',
          [-1]: '退回'
        };
        this.rectifyProcess(params).then((rs)=>{
          window.console.log(123123,rs);
          if(rs) {
            this.$message.success(`${this.stage} 流程${ type[status] || '执行' }成功 ！`);
            this.resetForm();
          } else {
            this.$message.info('表单接口请求失败！')
          }
        }).finally(()=>{
          this.$emit('cancel');
          // this.$emit('refreshTable');
          this.formLoading = false;
        });
      },
      // 重置
      resetForm() {
        this.$refs['ruleForm'].resetFields();
        this.$emit('cancel');
      },
      // 上传校验
      beforeUpload(file) {
        window.console.log(file, '上传校验')
        let testmsg = file.name.substring(file.name.lastIndexOf('.')+1)
        const extension = ['xls', 'xlsx', 'docx', 'pdf'];
        const isLt2M = file.size / 1024 / 1024 < 30
        
        if(!extension.includes(testmsg)) {
            this.$message({
                message: '上传文件只能是 xls、xlsx、docx、pdf格式!',
                type: 'warning'
            });
        }
        if(!isLt2M) {
            this.$message({
                message: '上传文件大小不能超过 30MB!',
                type: 'warning'
            });
        }
        return extension.includes(testmsg) && isLt2M;
      },
      handleRemove(file, fileList) {
        window.console.log(file, fileList);
      },
      handlePreview(file) {
        window.console.log(file);
      },
      // 上传失败
      handleError() {
        this.$message.error(`附件上传失败 !`);
      },
      // 上传成功
      handleSuccess(file, fileList) {
        window.console.log(file, fileList);
        if(Array.isArray(this.formData.fileList) && this.formData.fileList.length !== 0) {
          this.formData.fileList.push({
            name: this.getFileName(Object.values(fileList.response.data).join(',')),
            url: Object.values(fileList.response.data).join(',')
          });
        } else {
          this.formData.fileList = [];
          this.formData.fileList.push({
            name: this.getFileName(Object.values(fileList.response.data).join(',')),
            url: Object.values(fileList.response.data).join(',')
          });
        }
        
        this.$message.success(`${ fileList.name } 上传成功！`);
      },
      // 关联法律法规等数据
      relationData() {
        // this.dialogTableVisible = true;
        let inMethod = () => {
          let defectProcessInfo = {
            formData: this.formData,
            stage: this.stage,
            isRectification: this.isRectification,
            defectIds: this.defectIds
          };
          localStorage.setItem("defectProcessInfo", JSON.stringify(defectProcessInfo));
          
          this.$router.push({
            path: '/regulationTable',
            query: {
              index: '2-2',
              id: this.$route.query.id,
              isRelation: '1',
              actNav: this.$route.query.actNav,
              params: this.$route.query.params
            }
          });
        };
        // 讲当前formData存储到本地
        if(this.formData.knowledgeIds && this.formData.knowledgeIds.length !== 0) {
          this.$confirm(`当前已关联 ${ this.formData.knowledgeIds.length } 条需修订的法律法规/制度规范，继续关联将覆盖已选关联数据！是否继续？`, '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            inMethod();
          }).catch(() => {});
        } else {
          inMethod();
        }
      },
      // 展示关联知识库表格
      showKnowTable() {
        let params = {
          knowledgeIds: this.formData.knowledgeIds
        };
        this.tableLoading = true;
        this.knowTabel = [];
        this.getProcessReviseTable(params).then((rs)=>{
          // window.console.log('知识库',rs);
          if(Array.isArray(rs) && rs.length !== 0) {
            this.knowTabel = rs;
          }
        }).finally(()=>{
          this.tableLoading = false;
          this.dialogTableVisible = true;
        });
      }
    }
  }
</script>

